import {instance} from "./api";

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId)
    },
    //lesson73          
    //          
    //   dal()  bll(     )
    //     UI   
    // 1.  -> 2.ProfileContainer
    getStatus(userId) {
        return instance.get(`profile/status/` + userId)

    },
    updateStatus(status) {
        return instance.put(`profile/status`, {
            status: status //(       status)
        })
    }


}